import BlockContainer from '../../BlockContainer/BlockContainer';

export default class Modal {
  constructor(id, classes, model) {
    this.id = id;
    this.classes = classes;
    this.model = model;
  }

  render() {
    const modal = document.createElement('div');
    modal.setAttribute('id', this.id);
    document.body.append(modal);

    const header = new BlockContainer(
      this.id,
      this.model.modalHeader.options.classes,
      this.model.modalHeader.content,
    );

    const body = new BlockContainer(
      this.id,
      this.model.modalBody.options.classes,
      this.model.modalBody.content,
    );

    const footer = new BlockContainer(
      this.id,
      this.model.modalFooter.options.classes,
      this.model.modalFooter.content,
    );

    header.render();
    body.render();
    footer.render();
  }
}
